  .SmtParameter input:-moz-placeholder,
  .SmtParameter textarea:-moz-placeholder {color: #DDDDDD;}
  .SmtParameter input::-moz-placeholder,
  .SmtParameter textarea::-moz-placeholder {color: #DDDDDD;}
  .SmtParameter input:-ms-input-placeholder,
  .SmtParameter textarea:-ms-input-placeholder {color: #DDDDDD;}
  .SmtParameter .van-collapse-item__content{padding: 0 .3rem}

</style>



