<template>
    <div class="ThinPanelParameter">
      <div class="FillOver both">
        <p>合计 <span class="cl_f90">$2000</span></p>
        <span @click="nextStep"  class="bg_f90 nextStep">下一步</span>
      </div>
      <PcbTop  :PcbTopTitle="PcbTitle"></PcbTop>
            <div class="Pcbhint">
              温馨提示：付款时间（非下单时间）在20点前交期从当天开始计算，20点后付款、20点30分后更新文件或21点确认工程问题，交期将增加1天；周六20点后和周日下单付款等同于周一下单付款。
            </div>
      <van-collapse v-model="activeName">
        <van-collapse-item title="薄膜开关价格和交期" name="1">
          <div class="lifecycle ">
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">生产周期</p>
              <p class="cl_333 f28">4天</p>
            </div>
            <div class="fiexover border-bD8D8D8">
              <p class="cl_666 f28">数量</p>
              <p class="cl_333 f28">10</p>
            </div>
            <div class="fiexover">
              <p class="cl_666 f28">价格</p>
              <p class="cl_333 f28">$200.00</p>
            </div>
          </div>
        </van-collapse-item>
        <van-collapse-item title="薄膜开关参数详情" name="2">
          <div class="lifecycle ">
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">尺寸大小</p>
              <p class="cl_333 f28">20 x 20cm</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">定制数量</p>
              <p class="cl_333 f28">50</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">材料</p>
              <p class="cl_333 f28">PC</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">材质</p>
              <p class="cl_333 f28">光滑表面</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">背胶</p>
              <p class="cl_333 f28">0.175</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">基材厚度</p>
              <p class="cl_333 f28">约0.9~1.3mm</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">颜色数量</p>
              <p class="cl_333 f28">1</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">凸点</p>
              <p class="cl_333 f28">有</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">是否有LED</p>
              <p class="cl_333 f28">无</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">LED数量</p>
              <p class="cl_333 f28">暂无数据</p>
            </div>
            <div class="fiexover border-bD8D8D8 ">
              <p class="cl_666 f28">线路凸点</p>
              <p class="cl_333 f28">银浆起凸</p>
            </div>

            <div class="fiexover border-bD8D8D8">
              <p class="cl_666 f28">按键数量</p>
              <p class="cl_333 f28">暂无数据</p>
            </div>
            <div class="fiexover">
              <p class="cl_666 f28">外形切割</p>
              <p class="cl_333 f28">激光切割</p>
            </div>
          </div>
        </van-collapse-item>
        <van-collapse-item  name="3">
          <template #title>
            <div>备注及内部编号 <i class="iconfont cl_7C5922">&#xe653;</i></div>
          </template>
          <div class="ParameterLayout">
            <textarea name="" cols="30" rows="10" class="" placeholder="如果订单有特殊要求，请填写在此处；若文件有问题，捷配会以手机短信和语音电话(来电号码：0731-89504185)通知“订单联系人”，为了不影响您的交期，请及时登录捷配会员或微信公众号查看并回复"></textarea>
          </div>
        </van-collapse-item>
      </van-collapse>
      <div class="fiexoverTitle mt24">
        <div class="fiexover">
          <p class="f28"> 自定义编号</p>
          <div class="f34 cl_333 h_100 lh102">
            <input type="text"  placeholder="请输入" v-model="nickname" class="bn text_right">
          </div>
        </div>
      </div>

    </div>

</template>

<script>
    import Vue from "vue"
    import PcbTop from "../../common/PcbTop";

    export default {
        name: "ThinPanelParameter",
        components: {PcbTop},
        data() {
            return {
              //像子组件传值
              PcbTitle:{name:"产品详情",class:true},
              activeName: ["1","2","3","4",],
              nickname:"",
            }
        },
        methods: {
          //下一步跳转
          nextStep(){
            console.log("1");
            this.$router.push({
              path:'PcbSucceed'
            })
          },
        },
        created() {

        }
    }

</script>

<style>
  .ThinPanelParameter .FillOver{z-index: 100;   position: fixed;bottom: 0;width: 100%;height: 1.12rem;line-height: 1.12rem;-webkit-box-shadow: 0px -3px 5px 0px rgba(0, 0, 0, 0.12);box-shadow: 0px -3px 5px 0px rgba(0, 0, 0, 0.12);}
  .ThinPanelParameter .FillOver p{display: inline-block;float: left;width: 50%;text-align: left;font-size: .34rem;background: #fff;color: #666;height: 100%;padding-left: .3rem;}
  .ThinPanelParameter .FillOver .nextStep{display: inline-block;float: left;width: 50%;background: #f90;color: #fff;font-size: .34rem;text-align: center;height: 100%;}
  .ThinPanelParameter .van-collapse-item{margin-top: .24rem;}
  .ThinPanelParameter .van-cell::after {border-bottom: none;}
  .ThinPanelParameter .van-collapse-item .van-cell{border-bottom: 1px solid #ebedf0;padding: 0 .3rem;line-height: 1.02rem}
  .van-collapse-item__content{padding: 0 .3rem;}
  .ThinPanelParameter .ParameterLayout{    background: #fff;padding: .3rem 0}
  .ThinPanelParameter .ParameterLayout textarea{    width: 100%;resize: none;height: 1.6rem;border: none;font-size: .28rem;}
</style>



